body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-style: italic;
}

.card-header{
  background-color: #f7f7f7;
}

.zooming {
  position: relative;  
  overflow: hidden;
}

.zooming img {
  max-width: 100%;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.zooming:hover img {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
      transform: scale(1.1);
}



